import React, { useState } from "react";
import { Link } from 'react-router-dom';
import AccordionItem from "./AccordionItem";

function SidebarMobile() {
    const [openAccordionIndex, setOpenAccordionIndex] = useState(null);

    function handleAccordionClick(index) {
        setOpenAccordionIndex(prevIndex => (prevIndex === index ? null : index));
    }

    return (
        <div className="side-bar-mobile" id="sidebarMobile">
            <div className="sidebar-items">
                <ul className="sidebar-navlinks">
                    <li><Link to="/">Home</Link></li>
                    <AccordionItem
                        title="About"
                        items={[
                            { label: "About OAUTHC", url: "/About/About-OAUTHC" },
                            { label: "Departments & Centers", url: "/About/Departments-Centers" },
                            { label: "Find a Doctor", url: "/About/find-doctor" },
                            { label: "Locations", url: "/About/locations" },
                            { label: "Patient & Visitor Guide", url: "/About/patient-visitor-guide" }
                        ]}
                        isOpen={openAccordionIndex === 0}
                        onClick={() => handleAccordionClick(0)}
                    />
                    <AccordionItem
                        title="Services"
                        items={[
                            { label: "Research & Ethics", url: "/services/service-1" },
                            { label: "Diseases & Symptoms", url: "/services/service-2" },
                            { label: "Tests & Procedures", url: "/services/service-3" },
                            { label: "Healthy Lifestyle", url: "/services/service-4" },
                            { label: "Drugs & Supplements", url: "/services/service-5" }
                        ]}
                        isOpen={openAccordionIndex === 1}
                        onClick={() => handleAccordionClick(1)}
                    />
                    <li><Link to="/blog">Blog</Link></li>
                    <li><Link to="/contact">Contact Us</Link></li>
                </ul>
                <div className="sidebar-buttons">
                    <Link to="/book-appointment">
                        <button type="button" className="book-appointment-button">
                            Book an Appointment
                        </button>  
                    </Link> 
                    <button type="button" className="student-portal-button">
                        <a href="https://btom7447.github.io/OAUTHC-WEBSITE/" target="_blank" rel="noopener noreferrer">
                            Log in to Student Portal
                        </a>
                    </button>

                </div>
            </div>
        </div>
    );
}

export default SidebarMobile;